<template>
  <div>
    <div class="search">
      <van-icon @click="up" class="search_up" name="arrow-left" />
      <van-search
        v-model="value"
        shape="round"
        background="#fff"
        placeholder="请输入搜索关键词"
        @input="oninput"
      />
      <div class="search_qvxiao" @click="onSearch">{{ searchText }}</div>
    </div>
    <div v-show="flag" class="search_list">
      <p>历史搜索记录</p>
      <ul>
        <li
          v-for="(item, index) in searchList"
          :key="index"
          @click="onli(item)"
        >
          {{ item }}
        </li>
      </ul>
    </div>
    <div v-show="!flag" class="searchData">
      <div
        class="lzh-container"
        v-for="(item, index) in list.list"
        :key="index"
      >
        <div class="lzh-teseke" @click="courseitem(item.id)">
          <div style="margin-left: 0.4rem">{{ item.title }}</div>
          <div style="margin-left: 0.4rem">共8课时</div>
          <div class="lzh-titlebox">
            <div class="lzh-titleleft">
              <img
                round
                width="1.5rem"
                height="1.5rem"
                :src="item.teachers_list[0].teacher_avatar"
              />{{ item.teachers_list[0].teacher_name }}
            </div>
            <div class="lzh-titleright">
              <img v-show="item.has_buy" src="/baoming.png" alt="" />
            </div>
          </div>

          <div class="lzh-titlebox" style="border-bottom: 0">
            <div class="lzh-titleleft">
              <div>132人报名</div>
            </div>
            <div class="lzh-titleright">
              <div style="color: aquamarine">免费</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchText: "搜索",
      value: "",
      list: [],
      flag: true,
      searchList: [],
    };
  },
  methods: {
    onSearch() {},
    up() {
      this.$router.go(-1);
    },
    //搜索
    onSearch() {
      let n = true;
      this.searchList.forEach((res) => {
        if (res == this.value) {
          n = false;
        }
      });
      if (n) {
        this.searchList.push(this.value);
      }
      this.$APP.courseSearch(this.value).then((res) => {
        console.log(res.data.data);
        this.list = res.data.data;
        if (res.data.data.list.length != 0) {
          this.flag = false;
        } else {
          this.flag = true;
        }
      });
    },
    //
    oninput() {
      if (this.value == "") {
        this.flag = true;
      }
    },
    //点击搜索记录搜索
    onli(item) {
      this.value = item;
      this.onSearch();
    },
  },
};
</script>

<style>
body {
  background: rgb(243, 243, 243);
}
.search {
  width: 100%;
  display: flex;
  background: white;
  align-items: center;
}
.search_up {
  width: 10%;
  text-align: center;
}
.van-search {
  width: 80%;
}
.search_qvxiao {
  width: 10%;
  font-size: 0.7rem;
}
.search_list {
  width: 100%;
  background: white;
}
.search_list p {
  font-size: 0.9rem;
  font-weight: 600;
}
.search_list ul {
  width: 90%;
  margin: 1rem auto 0;
  display: flex;
}
.search_list ul li {
  font-size: 0.6rem;
  background: lightgray;
  border-radius: 10px;
  padding: 0.2rem;
  margin: 0.2rem;
}
.lzh-container {
  width: 100%;
  height: 6rem;
  background: #fff;
  border-radius: 5px;
  margin-top: 0.5rem;
  display: inline-flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
}
.lzh-container span {
  width: 3rem;
  height: 1.5rem;
  line-height: 1.5rem;
  background: #f5f5f5;
  text-align: center;
  margin-right: 0.7rem;
  margin-bottom: 0.8rem;
}
.lzh-butbox {
  width: 100%;
}
.lzh-butbox {
  width: 100%;
  padding: 0.5rem;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
}
.lzh-butbox button {
  width: 6rem;
  height: 1.3rem;
}
.lzh-title-box {
  padding: 0.5rem;
  background: #f0f2f5;
}
.lzh-teseke {
  width: 100%;
  height: 100%;
  font-size: 0.7rem;
}
.lzh-titlebox {
  width: 90%;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgb(177, 175, 175);
  margin-left: 0.7rem;
}
.lzh-titleleft {
  width: 40%;
  height: 1.5rem;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
}
.lzh-titleleft img {
  width: 1.5rem;
  height: 1.5rem;
}
.lzh-tilteright {
  width: 60%;
}
.lzh-titleright img {
  width: 3rem;
}
</style>